import React, { useState, useEffect, useCallback } from 'react';

const InfiniteScrollComponent = () => {
  const [items, setItems] = useState([]);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetchData(page);
  }, [page]);

  const fetchData = async (page) => {
    setLoading(true);
    // Replace with your API call
    // const response = await fetch(`https://api.example.com/items?page=${page}`);
    const response = await fetch(`https://api.unsplash.com/photos?client_id=${process.env.REACT_APP_UNSPLASH_API_KEY}`);
    // const data = await response.json();
    const data = [{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:1},{slug:2},{slug:2},{slug:2},{slug:2},{slug:2},{slug:2},{slug:2},{slug:2},{slug:2},{slug:2},{slug:2},{slug:2},{slug:2},{slug:2},{slug:2},{slug:3}];
    setItems(prevItems => [...prevItems, ...data]);
    setLoading(false);
  };

  const handleScroll = useCallback(() => {
    if (window.innerHeight + document.documentElement.scrollTop !== document.documentElement.offsetHeight || loading) return;
    setPage(prevPage => prevPage + 1);
  }, [loading]);

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [handleScroll]);

  return (
    <div>
      {items.map((item,i) => (
        <div key={i}>{item.slug}</div>
      ))}
      {loading && <div>Loading...</div>}
    </div>
  );
};

export default InfiniteScrollComponent;
