// client/src/ImageUpload.js
import React, { useState } from 'react';
import axios from 'axios';
import moment from "moment"

import './Search.css'

const Search = () => {
  const [searchInput, setSearchInput] = useState('');
  const [searchResult, setSearchResult]=useState([])

  const onChangeSearchInput = (event) => {
    setSearchInput(event.target.value);
  };

  const searchSimilar = () => {
    console.log(searchInput)

    axios.post('http://20.218.146.247:5000/search', { content: searchInput },{
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(response => {
        console.log(response.data);
        setSearchResult(response.data.receivedContent.matches.map(i=>i.metadata))
      })
      .catch(error => {
        console.error(error);
      });
  };

  return (
    <div className="container">
        <div className="search-bar-container">
            <div className="search-wrapper">
                <input type="text" className="search-bar" placeholder="Search..." onChange={onChangeSearchInput}/>
                <button className="search-button" onClick={searchSimilar}>Search</button>
            </div>
        </div>
        <div className="card-container">
            {searchResult.map(i=>{
              return (
              <a key={i.postLink} href={i.postLink} className="card" target="_blank">
                <img src={i.AzureImageLink} alt="Card Image" className="card-image" />
                <div className="card-content">
                    <h2 className="card-title">{moment(i.date, "YYYY年M月DD日").format("MMMM D, YYYY")}</h2>
                    <p className="card-description">{i.imageShortDescription}</p>
                </div>
              </a>
              )
            })}
            
        </div>
    </div>
  );
};

export default Search;
