import React, { useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useScroll } from './ScrollContext';

import ImageGallery from "react-image-gallery";
import "react-image-gallery/styles/css/image-gallery.css";

export default function App() {

    const navigate = useNavigate();
  const location = useLocation();
  const { scrollPositions, setScrollPositions } = useScroll();
  console.log('gallery',scrollPositions)
  useEffect(() => {
    const handleBeforeUnload = () => {

        console.log('to gallery')
      setScrollPositions(prev => ({
        ...prev,
        [location.pathname]: window.scrollY
      }));
    };

    window.addEventListener('beforeunload', handleBeforeUnload);
    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
  }, [location.pathname, setScrollPositions]);

  const goBack = () => {
    setScrollPositions(prev => ({
      ...prev,
      [location.pathname]: window.scrollY
    }));
    navigate(-1);
  };

  useEffect(() => {
    console.log(scrollPositions)
    if (scrollPositions[location.pathname] !== undefined) {
      window.scrollTo(0, scrollPositions[location.pathname]);
    }
  }, [location.pathname, scrollPositions]);

  const images = [
    {
      original: "https://picsum.photos/id/1018/1000/600/",
      thumbnail: "https://picsum.photos/id/1018/250/150/",
      description: 'hhhhhhhhhhhhhh'
    },
    {
      original: "https://picsum.photos/id/1015/1000/600/",
      thumbnail: "https://picsum.photos/id/1015/250/150/",
      description: 'ddddddddddddddddd'

    },
    {
      original: "https://picsum.photos/id/1019/1000/600/",
      thumbnail: "https://picsum.photos/id/1019/250/150/",
      description: ''

    }
  ];

  return (
    <div className="App">
              <button onClick={goBack}>Go Back</button>


      <h1>Hello CodeSandbox</h1>
      <h2>Start editing to see some magic happen!</h2>
      <ImageGallery
        items={images}
        showPlayButton={true}
        showFullscreenButton={true}
        slideInterval={1000}
        slideOnThumbnailOver={true}
        showIndex={true}
        
        onPlay={() => {
          alert("slideshow is now playing!");
        }}
      />
    </div>
  );
}
