        "firstComment": "Dragon available @theion3gallery  Sumi and acrylic  600 gr water colour paper  105 x 75 cm.  @theion3gallery #theion3gallery @checkerdemontattoos @checkerdemonpress #lukeatkinson #checkerdemontattoos #checkerdemonpress #stuttgarttattoo #japanesedragonpainting #japanesedragon #sumidragon",
