        "firstComment": "Fun cover made by Filip Leu and myself for the new Tattoolife Yearbook.  Cheers Dirk-Boris and Mikilooks great!#filipleu @checkerdemontattoos  @checkerdemonpress  @mikivialetto @dirkboris @tattoolifemagazine",
