        "imageShortDescription": "This tattoo features two koi fish, one dark and one fiery red-orange, in a dynamic swimming pose. Set on the side of the body, it showcases intricate scales and flowing water elements in blues and whites. Enhanced with lotus flowers, it reflects traditional Japanese artistry and personal expression through detailed linework and vibrant color shading.",
