        "firstComment": "Jürgen popped in today,good to see all settled in,wanna get some Japanese work?@checkerdemontattoos @checkerdemonpress #lukeatkinson #japanese bodysuit #irezumi #horimono #koiback #stuttgarttattoos #tattoostuttgart #0711 #germanytattooers #japanesetattoo",
