        "firstComment": "Koi arm from another life #lukeatkinson @checkerdemontattoos @checkerdemonpress @japanesetattoos @japanesesleeve @koitattoos irezumi @horimono @stuttgarttattoos @tattoosstuttgart @0711 @germanytattooers",
