        "firstComment": "Checker Demon Press is proud to present out latest collaboration with Titine Leu “Dreams Arising” 60 x 40 cm. Giclée Prints are now available in a small run.Please dm for further information.  Titine is currently in an online Show with @theion3gallery together with @aialeu  @aimeecornwelltattoo  @leuloretta and @sabinegaffron  ______________________________________________  @checkerdemonpress",
